import { createApp } from 'vue'
import { createStore } from 'vuex'

import App from './App.vue'
import MainPage from './components/MainPage.vue'
import DegustationPage from './components/DegustationPage.vue'
import AboutUsPage from './components/AboutUsPage.vue'
import OfferPage from './components/OfferPage.vue'
import ContactPage from './components/ContactPage.vue'
import CollectionPage from './components/CollectionPage.vue';
import CorporativePage from './components/CorporativePage.vue';

import { createWebHistory, createRouter } from "vue-router";

import './assets/css/style.css';

const app = createApp(App)

const routes = [

  {
    path: '/',
    name: 'MainPage',
    component: MainPage,
  },
  {
    path: '/degustation',
    name: 'DegustationPage',
    component: DegustationPage,
  },
  {
    path: '/aboutus',
    name: 'AboutUsPage',
    component: AboutUsPage,
  },
  {
    path: '/offer',
    name: 'OfferPage',
    component: OfferPage,
  },
  {
	path: '/contact',
	name: 'ContactPage',
	component: ContactPage,
  },
  {
	path: '/collection',
	name: 'CollectionPage',
	component: CollectionPage,
  },
  {
	path: '/corporative',
	name: 'CorporativePage',
	component: CorporativePage,
  },
];

const router = createRouter({
  history: createWebHistory('/app'),
  routes,
});
router.beforeEach((to, from, next) => {
	window.scrollTo(0, 0);
	next();
});


/* store start */

const store = createStore({
  state () {
    return {
//         api_url: 'http://127.0.0.1:8000',
        /* api_url: 'http://192.168.1.141:8000', */
		api_url: 'http://shenvize.ru',
        main_slider_controller: null,
        second_slider_controller: null,
        /* bool variables */
        main_phone: '+79782720200',
        main_phone_display: "+7 (978) 272 02 00",
		email: 'rcvs@mail.ru',	
        mobile_header_show: false,
		age_checked: false,
    }
  },
  mutations: {
	  setAgeChecked (state, is_checked) {
		state.age_checked = is_checked;
		localStorage.age_checked = is_checked;
	  },
      setMainSlider (state, slider) {
          console.log('init slider', slider)
          this.state.main_slider_controller = slider;
      },
      setSecondSlider (state, slider) {
          console.log('init second slider', slider)
          this.state.second_slider_controller = slider;
      },
      setMobileHeaderShow(state, is_show) {
          this.state.mobile_header_show = is_show
      },
  },
  /* getters: {
  } */
})

/* store end */

/* initialize everything to app */
app.use(store)
app.use(router)
app.mount('#app')
