<strong>Егермейстер</strong> (от нем. Jägermeister — егермейстер, дословно — главный егерь) — немецкий крепкий (35 % крепости) ликёр, настоянный на травах.
Относится к категории биттеров. Ликёр получается путём настаивания 56 компонентов — растений, кореньев, корок — и выдерживается около 12 месяцев в дубовых бочках. Точный состав напитка держится в секрете. На этикетке бутылки с ликёром изображается олень с крестом в рогах, по легенде привидевшийся святому Губерту, покровителю охотников. Этикетка обрамлена стихотворением немецкого писателя и лесничего Оскара фон Ризенталя. В логотипе используется стилизованная под старину запись названия как Jägermeiſter с использованием длинного s.
Егермейстер, как и многие другие биттеры, считается способствующим пищеварению. Употребляется охлаждённым в чистом виде (в шотах) залпом (как дижестив) либо в коктейлях. 

