<template>

      <MobileHeader />
      <router-view></router-view>
      <Footer />

	<AgeCheckDialog />
</template>

<script>

import MobileHeader from './components/MobileHeader.vue';
import Footer from './components/Footer.vue';
import AgeCheckDialog from './components/AgeCheckDialog.vue';

export default {
  name: 'App',
  async beforeMount () {	
	await this.setLocalStorage();
	console.log(localStorage)
	console.log(localStorage.age_checked)
  },
  mounted () {
	console.log('age is checked', this.age_checked);
	console.log('check for bool', this.age_checked == false)
  },
  components: {
    MobileHeader,
    Footer,
	AgeCheckDialog,
  },
  computed: {
	age_checked () {
		if (this.$store.state.age_checked == "true") {
			return true;
		} else {
			return false;
		}
	}
  },
  methods: {
	setLocalStorage () {
		if (!localStorage.age_checked) {
			localStorage.age_checked = false; 
		} else {
			this.$store.commit('setAgeChecked', localStorage.age_checked);
		}
	},
  },
}
</script>

