<template>
  <swiper class="main_page_main_slider"
    @swiper="this.setControlledSwiper"
    :loop="true"
    :slides-per-view="1"
    :space-between="50"
    navigation.nextEl=".main_slider_next"
    navigation.prevEl=".main_slider_prev"
  >
    <swiper-slide
    class="main_page_main_slider_slide">

			<img class="absolute z-10 block object-contain w-1/4 h-full max-h-40 top-3 md:hidden left-1/2 transform -translate-x-1/2"
			:src="this.api_url+'/static/images/vinokuri_logo_color.png'"/>
        <div class="absolute z-10 w-11/12 text-white left-1/2 transform -translate-x-1/2 bottom-12 md:top-2/4">
            <div class="container my-4 text-2xl md:text-5xl">
				ВИНОКУРНЯ
				<span class="gothic">
				«ШЕНВИЗЕ»
				</span>
				- ПРИДИ И ЗАБУДЬ ОБО ВСЕМ!
            </div>
            <div class="main_slider_text__subtitle">
              Только настоящий шнапс, обстлер, eгермейстер, немецкий крюшон, киршвассер, только из местного сырья и только своими собственными руками!
            </div>
        </div>
        <img class="slider_main_image" 
		:src="this.api_url + '/static/images/main_slider_1.jpeg'" alt="">
    </swiper-slide>

    <swiper-slide
    class="main_page_main_slider_slide">
        <div class="main_slider_text">
            <div class="container my-4 text-3xl md:text-4xl">
              Зал нашей винокурни выполнен в немецком стиле «фахверк». <br>
              Здесь уютно, комфортно и солнечно!
            </div>
        </div>
        <img class="slider_main_image" 
		:src="this.api_url + '/static/images/B01.jpg'" alt="">
    </swiper-slide>
    <swiper-slide
    class="main_page_main_slider_slide">
        <div class="main_slider_text">
            <div class="container my-4 text-3xl md:text-4xl">
              Наши напитки изготавливаются исключено на медном оборудовании <br>
              по рецептам, которые передаются столетиями от отца к сыну!
            </div>
        </div>
        <img class="slider_main_image"
		:src="this.api_url + '/static/images/B05.jpg'" alt="">
    </swiper-slide>

    <div class="main_slider_navigation">
        <div class="main_slider_navigation__button pointer"
        @click="this.slidePrev()">
            <ArrowLeftIcon class="w-6 h-6 text-white" />
        </div>
        <div class="main_slider_navigation__button pointer"
        @click="this.slideNext()">
            <ArrowRightIcon class="w-6 h-6 text-white" />
        </div>
    </div>

  </swiper>

</template>
<script>
  // Import Swiper Vue.js components
  import { ArrowLeftIcon, ArrowRightIcon } from '@heroicons/vue/solid'
  import SwiperCore, { Navigation } from 'swiper';
  import { Swiper, SwiperSlide } from 'swiper/vue';

  // Import Swiper styles
  import 'swiper/swiper.scss';
  import 'swiper/components/navigation/navigation.scss';

    SwiperCore.use([Navigation])
  export default {
    name: 'MainSlider',
    computed: {
        main_slider_controller () {
            return this.$store.state.main_slider_controller;
        },
        api_url () {
          return this.$store.state.api_url;
        },
    },
    components: {
      ArrowLeftIcon,
      ArrowRightIcon,
      Swiper,
      SwiperSlide,
    },
    methods: {
        setControlledSwiper(swiper) {
            this.$store.commit('setMainSlider', swiper)
        },
        slideNext () {
            this.main_slider_controller.slideNext()
        },
        slidePrev () {  
            this.main_slider_controller.slidePrev()
        }
    },
  };
</script>
