<template>
  <TransitionRoot appear :show="isOpen" as="template">
    <Dialog as="div" @close="tryClose">
      <div class="fixed inset-0 z-10 overflow-y-auto">
        <div class="min-h-screen px-4 text-center">
          <TransitionChild
            as="template"
            enter="duration-300 ease-out"
            enter-from="opacity-0"
            enter-to="opacity-100"
            leave="duration-200 ease-in"
            leave-from="opacity-100"
            leave-to="opacity-0"
          >
            <DialogOverlay class="fixed inset-0" />
          </TransitionChild>

          <span class="inline-block h-screen align-middle" aria-hidden="true">
            &#8203;
          </span>

          <TransitionChild
            as="template"
            enter="duration-300 ease-out"
            enter-from="opacity-0 scale-95"
            enter-to="opacity-100 scale-100"
            leave="duration-200 ease-in"
            leave-from="opacity-100 scale-100"
            leave-to="opacity-0 scale-95"
          >
            <div
              class="inline-block w-full max-w-md p-6 my-8 overflow-hidden text-left align-middle bg-white shadow-xl transition-all transform rounded-2xl"
            >
			<img 
			class="object-contain w-10/12 h-24 pb-4 mx-auto"
			:src="this.api_url + '/static/images/vinokuri_logo_color.png'"
			/>
			<div class="text-5xl font-bold text-center text-red-800 md:text-6xl">
				18+
			</div>
              <DialogTitle
                as="h3"
                class="text-lg font-medium text-gray-900 leading-6"
              >
              </DialogTitle>
              <div class="mt-2">
                <p class="text-sm text-gray-500">
				{{ this.description }}
                </p>
              </div>

              <div class="flex justify-around w-full mt-8">
                <button
                  type="button"
                  class="inline-flex justify-center px-4 py-2 text-xs font-medium text-green-900 bg-green-100 border border-transparent md:text-sm rounded-md hover:bg-green-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-green-500"
                  @click="checkAge"
                >
                 {{ this.button_text }} 
                </button>

                <button
                  type="button"
                  class="inline-flex justify-center px-4 py-2 text-xs font-medium text-red-900 bg-red-100 border border-transparent md:text-sm rounded-md hover:bg-red-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-red-500"
                  @click="checkAgeNot"
                >
				Покинуть сайт	
                </button>

              </div>
            </div>
          </TransitionChild>
        </div>
      </div>
    </Dialog>
  </TransitionRoot>
</template>

<script>

import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
  DialogTitle,
} from '@headlessui/vue'

export default {
  name: 'CallPhoneModal',
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
    DialogTitle,
  },
  computed: {
	api_url () {
		return this.$store.state.api_url;
	},
    isOpen () {
	if (this.$store.state.age_checked == "false") {
		return true;
	}
	if (this.$store.state.age_checked == false) {
		return true;
	}
		return false;
    },
	button_text () {
		return 'Мне есть 18';
	},
	heading () {
		return 'Подтверждение возраста';
	},
	description () {
		return 'Сайт содержит информацию для совершеннолетних лиц и доступен только после подтверждения возраста!';
	},
  },
  methods: {
	checkAge () {
		this.$store.commit('setAgeChecked', true);
	},
	checkAgeNot () {
		window.location.href = "https://google.com";
	},
	tryClose () {
		console.log('try to close modal')
	},
  }
}
</script>
